import React from 'react';
import AppComponent from '../components/AppComponent';
import Header from '../components/Header';
import GuardedContainer from '../components/GuardedContainer';
import {Row, Form, FormControl, ControlLabel, FormGroup, Button, Modal, Radio} from 'react-bootstrap';
import FormElement from '../components/FormElement';
import {authenticate} from '../services/Authentication';
import {isValidForm} from '../components/utilities';


class Login extends AppComponent {

  constructor(props) {
    super(props);
    this.state = {'link':'register'};
    this.inputs = {};
    this.handleSubmit = this.handleSubmit.bind(this);
    this.showModal = this.showModal.bind(this);
    this.setLink = this.setLink.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  showModal() {
    this.setState({'showModal':true});
  }

  closeModal() {
    this.setState({'showModal':false});
  }

  async handleSubmit() {
    this.setState({hasError:false});
    if(isValidForm('login')) {
      //authenticate(this.inputs);
    } else {
      this.setError('Please fill in the highlighted fields.');
    }
  }

  setLink(event) {
    this.setState({
      'link':event.target.value,
      'selectedOption':event.target.value
    })
    //this.setState({'link':link});
  }

  render() {
    return(
      <div>
        <GuardedContainer pageHeader="Login" column='4'>
            <Form id="login">
            <Row>
            <FormGroup className="col-md-12">
              <ControlLabel>User Id</ControlLabel>
              <FormControl className="col-md-6" id="userId" type="text" name="userId" onChange={this.handleInputChange} mandatory="true">
              </FormControl>
            </FormGroup>
            </Row>

            <Row>
            <FormGroup className="col-md-12">
              <ControlLabel>Password</ControlLabel>
              <FormControl className="col-md-6" id="password" type="password" name="password" onChange={this.handleInputChange} mandatory="true">
              </FormControl>
            </FormGroup>
            </Row>
            <Button bsStyle="info" onClick={this.handleSubmit}>Login</Button>
            <Button className="pull-right" bsStyle="link" onClick={this.showModal}>New Users register here</Button>
            </Form>
            {this.state.showModal && <div className="static-modal">
              <Modal.Dialog>
                <Modal.Header>
                  <Modal.Title>Please select your sector type from the following:<span className="pull-right" style={{'cursor':'pointer'}} onClick={this.closeModal}>X</span></Modal.Title>

                </Modal.Header>

                <Modal.Body>
                <FormGroup>
                  <Radio type="radio" name="registerGroup" value="register" checked={this.state.selectedOption == 'register'} onChange={this.setLink}>
                    Manufacturing
                  </Radio>{' '}
                  <Radio type="radio" name="registerGroup" value="register_service" checked={this.state.selectedOption == 'register_service'} onChange={this.setLink}>
                    Service
                  </Radio>
                </FormGroup>
                </Modal.Body>

                <Modal.Footer>
                  <Button bsStyle="primary" href={this.state.link}>Register</Button>
                </Modal.Footer>
              </Modal.Dialog>
            </div>}
        </GuardedContainer>
      </div>
    );
  }

}

export default Login;



